<script>
import header from "@/components/Movie-header";
import footer from "@/components/Movie-footer";

export default {
    name: 'MovieDetails',
    components: {
        "Movie-header": header,
        "Movie-footer": footer
    },
    data() {
        return {
            id: this.$route.params.id,
            api: this.$store.state.api,
            list: {},
        }
    },
    mounted() {

        this.axios(`${this.api}/api/detailmovie?movieId=${this.id}`)
            .then(res => {
                this.list = res.data.data.detailMovie
            })
            .catch(err => {
                console.log(err)
            })




    },
}
</script>

